import React, { useState, useEffect, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ConfigP1 from './ConfigP1';
import ConfigP2 from './ConfigP2';
import ConfigP3 from './ConfigP3';
import ConfigP4 from './ConfigP4';
import { socket } from '../context/SocketContext';
import { useSnackbar } from 'notistack';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';

const useStyles = makeStyles((theme) => ({
    layout: {
        width: '1000px',
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(6),
            marginBottom: theme.spacing(6),
            padding: theme.spacing(3),
        },
    },
    stepper: {
        padding: theme.spacing(3, 0, 5),
    },
    buttons: {
        display: 'flex',
        justifyContent: 'flex-end',
    },
    button: {
        marginTop: theme.spacing(3),
        marginLeft: theme.spacing(1),
    },
}));

const steps = ['Exchange API', 'Coin Settings', 'Starting Balance', 'General Settings'];

export default function ConfigMain() {
    const classes = useStyles();
    const [configuration, setConfiguration] = useContext(ConfigurationContext);
    const { enqueueSnackbar } = useSnackbar();
    const [activeStep, setActiveStep] = useState(0);
    const [input, setInput] = useContext(ConfigurationInputContext);
    const [startingBalance, setStartingBalance] = useContext(StartingBalanceContext);

    useEffect(() => {
        setInput({ ...input, recreate: false });

        return () => {
        }
    }, []);

    const handleNext = () => {
        let error = false;
        if (activeStep === 0) getPortfolio();
        if (activeStep === 3) {
            if (input.botname.length === 0) {
                error = true;
                enqueueSnackbar('Bot name must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                });
            }
            if (Number(input.threshold) === 0) {
                error = true;
                enqueueSnackbar('Threshold must be defined', {
                    variant: 'error',
                    autoHideDuration: 8000,
                });
            }
            if (!error) {
                storeConfiguration();
                // Wait for server confirmation below
            }
        }
        if (activeStep !== 3) setActiveStep(activeStep + 1);
    };

    useEffect(() => {
        socket.on("configuration-stored", () => {
            setInput((prevState => {
                return {
                    ...prevState,
                    apiKey: '************',
                    apiSecret: '************',
                }
            }));
            // Reset starting balances to trigger reload (in case coins were added to portfolio)
            setStartingBalance([]);

            setActiveStep(4);
        });

        return () => {
            socket.off("configuration-stored");
        }
    }, []);

    const handleBack = () => {
        if (activeStep === 2) getPortfolio();
        setActiveStep(activeStep - 1);
    };

    const handleInput = (e) => {
        let newInput = input;
        newInput[e.field] = e.input;
        setInput(newInput);
    }

    const getPortfolio = () => {
        if (socket) socket.emit("get-portfolio", {
            apiKey: input.apiKey,
            apiSecret: input.apiSecret,
        });
    }
    const storeConfiguration = () => {
        if (socket) {
            socket.emit("store-configuration", input);
            setConfiguration(input);
        }
    }

    function getStepContent(step) {
        // step = 3;
        switch (step) {
            case 0:
                return <ConfigP1 callback={handleInput} />;
            case 1:
                return <ConfigP2 callback={handleInput} />;
            case 2:
                return <ConfigP3 callback={handleInput} />;
            case 3:
                return <ConfigP4 callback={handleInput} />;
            default:
                throw new Error('Unknown step');
        }
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <main className={classes.layout}>
                <Paper className={classes.paper}>
                    <Stepper activeStep={activeStep} className={classes.stepper}>
                        {steps.map((label) => (
                            <Step key={label}>
                                <StepLabel>{label}</StepLabel>
                            </Step>
                        ))}
                    </Stepper>
                    <React.Fragment>
                        {activeStep === steps.length ? (
                            <React.Fragment>
                                <Typography variant="h5" gutterBottom>
                                    Configuration has been saved
                                </Typography>
                                <Typography variant="subtitle1">
                                    You can now leave this page
                                </Typography>
                            </React.Fragment>
                        ) : (
                            <React.Fragment>
                                {getStepContent(activeStep)}
                                <div className={classes.buttons}>
                                    {activeStep !== 0 && (
                                        <Button onClick={handleBack} className={classes.button}>
                                            Back
                                        </Button>
                                    )}
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        onClick={handleNext}
                                        className={classes.button}
                                    >
                                        {activeStep === steps.length - 1 ? 'Save Configuation' : 'Next'}
                                    </Button>
                                </div>
                            </React.Fragment>
                        )}
                    </React.Fragment>
                </Paper>
            </main>
        </React.Fragment>
    );
}