import React, { useContext, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TimelineView from "../components/TimelineView";
import StatusView from "../components/StatusView";
import LineChartCoins from '../components/LineChartCoins';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { socket } from '../context/SocketContext';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
}));

const CoinAnalysis = () => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const startingBalance = useContext(StartingBalanceContext)[0];

  useEffect(() => {
    if (socket && startingBalance.length === 0) socket.emit("get-starting-balances");
    return () => {
    }
  }, []);

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Typography variant="h3" noWrap>
            Coin Analysis
        </Typography>
        </Grid>
        <Grid item xs={4}>
          <TimelineView data={performance} />
        </Grid>
        <Grid item xs={4}>
          <StatusView />
        </Grid>
        <Grid item xs={12}>
          <Paper style={{ height: '700px' }} className={classes.paper}>
            <LineChartCoins performance={performance} balances={balances} title={'Coin BTC Price Changes (%)'} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}

export default CoinAnalysis;