import React, { useState, useEffect, useContext } from "react";
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { FiatContext } from '../context/FiatContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';

export default function StatusView(props) {
    const startingBalance = useContext(StartingBalanceContext)[0];
    const [performanceSummary, setPerformanceSummary] = useState({});
    const balances = props.balances;
    const type = props.type;
    const fiat = useContext(FiatContext)[0];
    let currency = (type === 'btc') ? 'BTC' : (type === 'fiat') ? fiat : 'Net BTC';
    const leftTxt = (type === 'net-result') ? 'Net change (BTC)' : `${currency} value`;
    const rightTxt = (type === 'net-result') ? 'Net change (%)' : `${currency} change`;

    useEffect(() => {
        let rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];

        let currentValue = 0;
        rows.forEach((coin) => {
            currentValue += coin.number * coin.last;
        });

        if (type === 'fiat') currentValue = currentValue * ((fiat === 'EUR') ? balances.eur : balances.usd);

        let change;
        if (type === 'btc') change = (currentValue / startingBalance.btc - 1) * 100;
        else if (fiat === 'EUR') change = (currentValue / startingBalance.eur - 1) * 100;
        else change = (currentValue / startingBalance.usd - 1) * 100;

        let netvalue = 0;

        if (startingBalance.coins) rows.forEach((coin) => {
            let c = startingBalance.coins.find(c => c.coin === coin.coin);
            if (c.coin) netvalue += c.number * coin.last;
        });

        if (type === 'net-result') {
            change = ((currentValue - netvalue) / startingBalance.btc) * 100
            currentValue = currentValue - netvalue;
        }

        if (change) {
            if (change < 100) change = change.toFixed(2)
            else if (change < 1000) change = change.toFixed(1);
            else change = change.toFixed(0);
        } else {
            change = '0';
        }
        if (currentValue) {
            if (currentValue < 0.1) currentValue = currentValue.toFixed(6)
            else if (currentValue < 1) currentValue = currentValue.toFixed(5)
            else if (currentValue < 10) currentValue = currentValue.toFixed(4)
            else if (currentValue < 100) currentValue = currentValue.toFixed(3)
            else if (currentValue < 1000) currentValue = currentValue.toFixed(2);
            else currentValue = currentValue.toFixed(2);
        } else {
            currentValue = '0';
        }

        setPerformanceSummary({
            value: currentValue,
            change: change,
        });
    }, [balances, startingBalance, fiat]);

    return (
        <Grid container alignItems="center">
            <Grid container item direction="column" alignItems="center" xs={6}>
                <Grid item xs={12}><Typography variant="h3">{performanceSummary.value}</Typography></Grid>
                <Grid item xs={12}><Typography>{leftTxt}</Typography></Grid>
            </Grid>
            <Grid container item direction="column" alignItems="center" xs={6}>
                <Grid item xs={12}><Typography variant="h3">{performanceSummary.change}%</Typography></Grid>
                <Grid item xs={12}><Typography>{rightTxt}</Typography></Grid>
            </Grid>
        </Grid>
    )
}