import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Checkbox from '@material-ui/core/Checkbox';
import { add } from 'date-fns'
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';

const useStyles = makeStyles((theme) => ({
    formControl: {
        minWidth: '100%',
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    textinput: {
        width: '380px',
    }
}));

export default function ConfigP4(props) {
    const classes = useStyles();

    const [input, setInput] = useContext(ConfigurationInputContext);

    const newBotname = (input.botname) ? input.botname : '';
    const newThreshold = (input.threshold) ? input.threshold : '';
    const newTimeshift = (input.timeshift) ? input.timeshift : '0';
    const newHeadless = (input.headless) ? input.headless : false;
    const newRunning = (input.running) ? input.running : true;

    const [botname, setBotname] = useState(newBotname);
    const [threshold, setThreshold] = useState(newThreshold);
    const [timeshift, setTimeshift] = useState(newTimeshift);
    const [headless, setHeadless] = useState(newHeadless);
    const [running, setRunning] = useState(newRunning);

    const getHour = (shift) => {
        const now = new Date();
        const current = now.getHours().toString().padStart(2, '0') + ":" + now.getMinutes().toString().padStart(2, '0');
        let adjusted = now;
        if (shift !== 0) adjusted = add(now, { hours: shift });

        const newt = adjusted.getHours().toString().padStart(2, '0') + ":" + adjusted.getMinutes().toString().padStart(2, '0');
        return [current, newt];
    }

    const [currenttime, setCurrenttime] = useState(getHour(newTimeshift)[0]);
    const [newtime, setNewtime] = useState(getHour(newTimeshift)[1]);

    const onHeadless = (e) => {
        props.callback({
            field: 'headless',
            input: e.target.checked,
        });
        setHeadless(e.target.checked);
    }

    const onRunning = (e) => {
        props.callback({
            field: 'running',
            input: e.target.checked,
        });
        setRunning(e.target.checked);
    }

    const onBotname = (e) => {
        props.callback({
            field: e.target.id,
            input: e.target.value
        });
        setBotname(e.target.value);
    }

    const onThreshold = (e) => {
        const pattern = /^[0-9]*\.?[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setThreshold(e.target.value);
        }
    }

    const onTimeshift = (e) => {
        props.callback({
            field: 'timeshift',
            input: e.target.value
        });
        setTimeshift(e.target.value);
        setNewtime(getHour(e.target.value)[1]);
    }

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        General Settings
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <HtmlTooltip
                        title={
                            <React.Fragment>
                                <Typography color="inherit" variant="h6">Hints</Typography>
                                <div>&nbsp;</div>
                                <div><b>{"Bot name"}</b></div>
                                <div>{"Give your bot a descriptive name. This is especially useful if you run multiple bots, so you can easily recognize each."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Balancing threshold"}</b></div>
                                <div>{"The balancing threshold is used by Balancing Bot to determine when to sell/buy coins. The threshold is the relative deviation from the target distribution at which point the bot will place an order to restore the coin to the target distribution."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Server timezone adjustment"}</b></div>
                                <div>{"If you're hosting your bot in the cloud, and the server's timezone is different from your local timezone, you can use this setting to adjust for that so that timestamps for orders and other events are provided in your local timezone."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Automatic mode"}</b></div>
                                <div>{"You can operate your bot manually. In this mode, you will need to keep an eye on the dashboard and place manual orders when the dashboard indicates that a coin should be bought or sold. This is useful if you want to have full control, typically when you use Balancing Bot for the first time and you want to get a feel for how it works."}</div>
                                <div>&nbsp;</div>
                                <div>{"Enabling automatic mode is a lot more practical though, as Balancing Bot will run 24/7 and place orders automatically to keep your portfolio balanced."}</div>
                                <div>&nbsp;</div>
                                <div><i>{"Note: "}</i>{"You don't need to keep your browser open to run your bot in automatic mode. The application will keep running in the background. You can open the application in the browser at any time to check the status of your bot."}</div>
                            </React.Fragment>
                        }
                    >
                        <InfoOutlinedIcon />
                    </HtmlTooltip>
                </Grid>
            </Grid>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="botname"
                        name="botname"
                        label="Bot Name"
                        helperText="Give your bot a name"
                        value={botname}
                        onChange={onBotname}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="threshold"
                        name="threshold"
                        label="Balancing Threshold (%)"
                        helperText="At what deviation from the target distribution should be balanced"
                        value={threshold}
                        onChange={onThreshold}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={12}>
                    <FormControl className={classes.formControl}>
                        <InputLabel id="server-timezone">Adjust for server timezone</InputLabel>
                        <Select
                            labelId="server-timezone"
                            value={timeshift}
                            onChange={onTimeshift}
                            className={classes.textinput}
                        >
                            <MenuItem value={12}>+12:00</MenuItem>
                            <MenuItem value={11}>+11:00</MenuItem>
                            <MenuItem value={10}>+10:00</MenuItem>
                            <MenuItem value={9}>+09:00</MenuItem>
                            <MenuItem value={8}>+08:00</MenuItem>
                            <MenuItem value={7}>+07:00</MenuItem>
                            <MenuItem value={6}>+06:00</MenuItem>
                            <MenuItem value={5}>+05:00</MenuItem>
                            <MenuItem value={4}>+04:00</MenuItem>
                            <MenuItem value={3}>+03:00</MenuItem>
                            <MenuItem value={2}>+02:00</MenuItem>
                            <MenuItem value={1}>+01:00</MenuItem>
                            <MenuItem value={0}>No adjustment</MenuItem>
                            <MenuItem value={-1}>-01:00</MenuItem>
                            <MenuItem value={-2}>-02:00</MenuItem>
                            <MenuItem value={-3}>-03:00</MenuItem>
                            <MenuItem value={-4}>-04:00</MenuItem>
                            <MenuItem value={-5}>-05:00</MenuItem>
                            <MenuItem value={-6}>-06:00</MenuItem>
                            <MenuItem value={-7}>-07:00</MenuItem>
                            <MenuItem value={-8}>-08:00</MenuItem>
                            <MenuItem value={-9}>-09:00</MenuItem>
                            <MenuItem value={-10}>-10:00</MenuItem>
                            <MenuItem value={-11}>-11:00</MenuItem>
                            <MenuItem value={-12}>-12:00</MenuItem>
                        </Select>
                        <FormHelperText>Current server time: {currenttime} - Adjusted server time: {newtime}</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="headless" checked={headless} onChange={onHeadless} />}
                        label="Run bot in automatic mode"
                    />
                </Grid>
                {/* <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="running" checked={running} onChange={onRunning} />}
                        label="Bot actived"
                    />
                </Grid> */}
            </Grid>
        </React.Fragment>
    );
}