import React, { useContext, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import BalancesTable from "../components/BalancesTable";
import PieChart from "../components/PieChart";
import { socket } from '../context/SocketContext';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import StatusView from "../components/StatusView";
import TimelineView from "../components/TimelineView";
import PerformanceSummary from "../components/PerformanceSummary";
import { StartingBalanceContext } from '../context/StartingBalanceContext';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
}));

const Portfolio = (props) => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const startingBalance = useContext(StartingBalanceContext)[0];

  useEffect(() => {
    if (socket && startingBalance.length === 0) socket.emit("get-starting-balances");
    return () => {
    }
  }, []);

  function handleOrder(selected) {
    socket.emit("market-order", selected);
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={3} alignItems="flex-start">
        <Grid item xs={4}>
          <Typography variant="h3" noWrap>
            Portfolio
        </Typography>
        </Grid>
        <Grid item xs={4}>
          <TimelineView data={performance} />
        </Grid>
        <Grid item xs={4}>
          <StatusView />
        </Grid>
        <Grid item xs={4}>
          <Paper style={{ height: '100px', minWidth: '420px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='btc' />
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper style={{ height: '100px', minWidth: '420px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='fiat' />
          </Paper>
        </Grid>
        <Grid item xs={4}>
          <Paper style={{ height: '100px', minWidth: '420px' }} className={classes.paper}>
            <PerformanceSummary balances={balances} type='net-result' />
          </Paper>
        </Grid>
        <Grid item xs={8}>
          <BalancesTable balances={balances} onOrder={handleOrder} />
        </Grid>
        <Grid container item xs={4}>
          <Grid item xs={12}>
            <Paper style={{ height: '500px' }} className={classes.paper}>
              <PieChart balances={balances} />
            </Paper>
          </Grid>
        </Grid>
      </Grid>
    </div >
  );
}

export default Portfolio;
