import React, { useContext } from 'react';
import { ResponsiveLine } from '@nivo/line'
import "./LineChart.css";
import { useTheme } from '@material-ui/core/styles';
import { FiatContext } from '../context/FiatContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';

export default function LineChart(props) {
    const mode = useTheme().palette.type;
    const startingBalance = useContext(StartingBalanceContext)[0];

    let performance = props.performance;
    let type = props.type;
    let title = props.title;
    const balances = props.balances;
    const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
    let values = [];
    let fiatValues = [];
    let change = [];
    let fiatChange = [];
    let netvalues = [];
    let netchange = [];
    let netvaluesfiat = [];
    let netchangefiat = [];
    let data1, data2 = [];
    let dayOne = [];
    const fiat = useContext(FiatContext)[0];

    rows.forEach((coin, i) => {
        dayOne[i] = {
            coin: coin.coin,
            initial: 0,
        };
    });

    let initialValue = 0;
    let initialFiatValue = 0;

    if (startingBalance.length !== 0) {
        initialValue = startingBalance.btc;
        initialFiatValue = (fiat === 'EUR') ? startingBalance.eur : startingBalance.usd;
    }

    if (performance.length !== 0) {
        performance.forEach((day) => {
            const fiatPrice = (fiat === 'EUR') ? day.eur : day.usd;
            let value = 0;
            let fiatValue = 0;
            day.coins.forEach((coin) => {
                value += coin.number * coin.price;
                fiatValue += coin.number * coin.price * fiatPrice;
            });

            let netvalue = 0;
            let netvaluefiat = 0;
            day.coins.forEach((coin) => {
                // Skip coins that are not in current portfolio
                if (dayOne.find(c => c.coin === coin.coin)) {
                    if (dayOne.find(c => c.coin === coin.coin).initial === 0) dayOne.find(c => c.coin === coin.coin).initial = coin.number;
                    netvalue += dayOne.find(c => c.coin === coin.coin).initial * coin.price;
                    netvaluefiat += dayOne.find(c => c.coin === coin.coin).initial * coin.price * fiatPrice;
                }
            })
            values.push({
                x: day.date,
                y: value,
            });
            change.push({
                x: day.date,
                y: (value / initialValue - 1) * 100,
            });
            fiatValues.push({
                x: day.date,
                y: fiatValue,
            });
            fiatChange.push({
                x: day.date,
                y: (fiatValue / initialFiatValue - 1) * 100,
            });
            netvalues.push({
                x: day.date,
                y: value - netvalue,
            });
            netchange.push({
                x: day.date,
                y: ((value - netvalue) / initialValue) * 100,
            });
            netvaluesfiat.push({
                x: day.date,
                y: fiatValue - netvaluefiat,
            });
            netchangefiat.push({
                x: day.date,
                y: ((fiatValue - netvaluefiat) / initialFiatValue) * 100,
            });
        });
    }
    if (type === 'btc') {
        data1 = [
            {
                id: 'Total Value (BTC)',
                data: values,
            }
        ];
        data2 = [
            {
                id: 'Total BTC Change (%)',
                data: change,
            }
        ];
    } else if (type === 'fiat') {
        title = title.replace('Fiat', `${fiat}`);
        data1 = [
            {
                id: `Total Value (${fiat})`,
                data: fiatValues,
            }
        ];
        data2 = [
            {
                id: `Total ${fiat} Change (%)`,
                data: fiatChange,
            }
        ];
    } else if (type === 'net-result') {
        data1 = [
            {
                id: 'Net Balancing Result (BTC)',
                data: netvalues,
            }
        ];
        data2 = [
            {
                id: 'Net Balancing Result (%)',
                data: netchange,
            }
        ];
    } else {
        title = title.replace('Fiat', `${fiat}`);
        data1 = [
            {
                id: `Net Balancing Result (${fiat})`,
                data: netvaluesfiat,
            }
        ];
        data2 = [
            {
                id: 'Net Balancing Result (%)',
                data: netchangefiat,
            }
        ];
    }

    const getTheme = () => {
        return {
            axis: {
                ticks: {
                    line: {
                        stroke: (mode === 'light') ? "#000000" : "#FFFFFF"
                    },
                    text: { fill: (mode === 'light') ? "#000000" : "#FFFFFF" }
                },
                legend: {
                    text: {
                        fill: (mode === 'light') ? "#000000" : "#FFFFFF"
                    }
                }
            },
            crosshair: {
                line: {
                    stroke: (mode === 'light') ? "#000000" : "#FFFFFF",
                },
            },
            tooltip: {
                container: {
                    background: (mode === 'light') ? "#FFFFFF" : "#000000",
                    color: (mode === 'light') ? "#000000" : "#FFFFFF",
                }
            }
        };
    };

    const getAxis1 = () => {
        return {
            legend: data1[0].id,
            legendOffset: -80,
            legendPosition: 'middle',
        }
    }

    const getAxis2 = () => {
        return {
            legend: data2[0].id,
            legendOffset: 70,
            legendPosition: 'middle',
        }
    }

    function uniq(a) {
        var seen = {};
        return a.filter(function (item) {
            return seen.hasOwnProperty(item) ? false : (seen[item] = true);
        });
    }

    return (
        <div className="wrapper">
            <div><b>{title}</b></div>
            <div className="secondGraph">
                {(startingBalance.length !== 0) ? (
                    <ResponsiveLine
                        data={data1}
                        sliceTooltip={({ slice }) => {
                            let background = (mode === 'light') ? "#FFFFFF" : "#000000";
                            let color = (mode === 'light') ? "#000000" : "#FFFFFF";
                            return (
                                <div
                                    style={{
                                        background: background,
                                        boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)',
                                        padding: '9px 12px',
                                    }}
                                >
                                    <div>Date: <strong>{slice.points[0].data.xFormatted}</strong></div>
                                    {uniq(slice.points).map(point => (
                                        <div
                                            key={point.id}
                                            style={{
                                                color: color,
                                                padding: '3px 0',
                                            }}
                                        >
                                            {point.serieId}: <strong>{point.data.yFormatted}</strong>
                                        </div>
                                    ))}
                                </div>
                            )
                        }}
                        theme={getTheme()}
                        margin={{ top: 40, right: 90, bottom: 50, left: 90 }}
                        xScale={{
                            type: 'time',
                            format: '%Y/%m/%d',
                            useUTC: false,
                            precision: 'day',
                        }}
                        xFormat="time:%Y/%m/%d"
                        yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                        yFormat=" >-.6f"
                        curve="monotoneX"
                        colors={['rgb(255,255,255,0)']}
                        axisTop={null}
                        axisRight={null}
                        axisBottom={null}
                        axisLeft={getAxis1()}
                        enablePoints={false}
                        enableGridX={false}
                        enableGridY={false}
                        useMesh={true}
                        // enableSlices="y"
                        crosshairType="cross"
                    />
                ) : (null)}
            </div>
            <div className="graphContainer">
                {(startingBalance.length !== 0) ? (
                    <ResponsiveLine
                        data={data2}
                        theme={getTheme()}
                        margin={{ top: 40, right: 90, bottom: 50, left: 90 }}
                        xScale={{
                            type: 'time',
                            format: '%Y/%m/%d',
                            useUTC: false,
                            precision: 'day',
                        }}
                        xFormat="time:%Y/%m/%d"
                        yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                        yFormat=" >-.6f"
                        curve="monotoneX"
                        lineWidth={4}
                        colors={['rgb(211,47,47)']}
                        axisTop={null}
                        axisBottom={null}
                        axisRight={getAxis2()}
                        axisLeft={null}
                        enablePoints={false}
                        enableGridX={false}
                        enableGridY={false}
                        useMesh={true}
                        // enableSlices="y"
                        crosshairType="cross"
                    />
                ) : (null)}
            </div>
        </div>
    )
}