import React, { useState, useEffect, useContext } from 'react';
import { withStyles, makeStyles, lighten } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Checkbox from '@material-ui/core/Checkbox';
import Slider from '@material-ui/core/Slider';
import Box from '@material-ui/core/Box';
import Skeleton from '@material-ui/lab/Skeleton';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';

import { socket } from '../context/SocketContext';

const iOSBoxShadow =
    '0 3px 1px rgba(0,0,0,0.1),0 4px 8px rgba(0,0,0,0.13),0 0 0 1px rgba(0,0,0,0.02)';

const MySlider = withStyles({
    root: {
        height: 2,
        padding: '15px 0',
    },
    thumb: {
        height: 28,
        width: 28,
        boxShadow: iOSBoxShadow,
        marginTop: -14,
        marginLeft: -14,
        '&:focus, &:hover, &$active': {
            boxShadow: '0 3px 1px rgba(0,0,0,0.1),0 4px 8px rgba(0,0,0,0.3),0 0 0 1px rgba(0,0,0,0.02)',
            // Reset on touch devices, it doesn't add specificity
            '@media (hover: none)': {
                boxShadow: iOSBoxShadow,
            },
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 12px)',
        top: 9,
        '& *': {
            background: 'transparent',
            color: '#FFFFFF',
        },
    },
    track: {
        height: 3,
    },
    rail: {
        height: 2,
        opacity: 0.5,
        backgroundColor: 'secondary',
    },
    mark: {
        backgroundColor: 'secondary',
        height: 8,
        width: 1,
        marginTop: -3,
    },
    markActive: {
        opacity: 1,
        backgroundColor: 'currentColor',
    },
})(Slider);

const headCells = [
    { id: 'coin', align: 'left', disablePadding: true, label: 'Coin' },
    { id: 'change', align: 'center', disablePadding: false, label: 'Change Distribution (%)' },
    { id: 'distribution', align: 'left', disablePadding: false, label: '' },
    { id: 'notifications', align: 'left', disablePadding: false, label: '' },
];

const marks = [
    {
        value: -1,
        label: '',
    },
    {
        value: 100,
        label: '',
    },
];

const useTableHeadStyles = makeStyles((theme) => ({
    headcolor:
        theme.palette.type === 'light'
            ? {
                backgroundColor: '#aaaaaa',
            }
            : {
                backgroundColor: '#777777',
            },
    headheight: {
        height: 44,
    },
}));

function EnhancedTableHead(props) {
    const { onSelectAllClick, numSelected, rowCount } = props;
    const classes = useTableHeadStyles();

    let checkbox;
    checkbox = <Checkbox
        indeterminate={numSelected > 0 && numSelected < rowCount}
        checked={rowCount > 0 && numSelected === rowCount}
        onChange={onSelectAllClick}
        inputProps={{ 'aria-label': 'select coins' }}
    />
    return (
        <TableHead>
            <TableRow className={`${classes.headcolor} ${classes.headheight}`}>
                <TableCell padding="checkbox">
                    {checkbox}
                </TableCell>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align={headCell.align}
                        padding={headCell.disablePadding ? 'none' : 'default'}
                    >
                        <TableSortLabel>
                            {headCell.label}
                        </TableSortLabel>
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}

EnhancedTableHead.propTypes = {
    classes: PropTypes.object.isRequired,
    numSelected: PropTypes.number.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    rowCount: PropTypes.number.isRequired,
};

const useToolbarStyles = makeStyles((theme) => ({
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        flex: '1 1 100%',
    },
}));

const EnhancedTableToolbar = (props) => {
    const classes = useToolbarStyles();
    const { selected, portfolio, setSelected } = props;
    const numSelected = selected.length;

    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            {numSelected > 0 ? (
                <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
                    {(numSelected === 1) ? 'Coin' : 'Coins'} <i>excluded</i> from balancing: {selected.join()}
                </Typography>
            ) : (
                <Typography className={classes.title} variant="subtitle1" id="tableTitle" component="div">
                    Select coins to be <i>excluded</i> from balancing
                </Typography>
            )}
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    selected: PropTypes.array.isRequired,
    portfolio: PropTypes.array.isRequired,
    setSelected: PropTypes.func.isRequired,
};

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    paper: {
        width: '800px',
        marginBottom: theme.spacing(2),
    },
    table: {
        maxWidth: 800,
        minWidth: 800,
        "& .MuiTableCell-sizeSmall": {
            paddingTop: "6px",
            paddingBottom: "0px",
        },
    },
    cell: {
        width: 50,
    },
    cell2: {
        width: 120,
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
}));

export default function ConfigP2(props) {
    const classes = useStyles();

    const [input, setInput] = useContext(ConfigurationInputContext);
    const newPortfolio = (input.portfolio) ? input.portfolio : [];
    const [portfolio, setPortfolio] = useState(newPortfolio);
    const [portfolioReceived, setPortfolioReceived] = useState(false);

    let newSelected = [];
    newPortfolio.forEach((coin) => {
        if (coin.excluded) newSelected.push(coin.coin);
    });
    const [selected, setSelected] = useState(newSelected);
    const precision = 2;

    const onInput = (data) => {
        props.callback({
            field: 'portfolio',
            input: data,
        });
    }

    useEffect(() => {
        socket.on("portfolio", data => {
            const numCoins = data.length;
            data.forEach((coin) => {
                // Check if coin was already in portfolio or is newly added to account
                const match = input.portfolio.find(c => c.coin === coin.coin);
                if (!match) {
                    // On initial configuration, don't exclude coins
                    if (input.apiKey === '************' && input.apiSecret === '************') {
                        coin.slider = -1;
                        coin.distribution = 0;
                        coin.excluded = true;
                        setSelected(prevState => [...prevState, coin.coin]);
                    } else {
                        coin.slider = -1;
                        coin.distribution = (100 / numCoins).toFixed(2);
                        coin.excluded = false;
                    }
                    coin.startnumber = coin.number;
                    coin.startprice = coin.price;
                } else {
                    // Keep previously set start number and start price
                    coin.slider = match.slider;
                    coin.distribution = match.distribution;
                    coin.excluded = match.excluded;
                    coin.startnumber = match.startnumber;
                    coin.startprice = match.startprice;
                }
            });
            setPortfolioReceived(true);
            setPortfolio(data);
            onInput(data);
        });

        return () => {
            socket.off("portfolio");
        }
    }, []);

    const handleSelectAllClick = (event) => {
        let newSelected = [];
        if (selected.length === 0) {
            newSelected = portfolio.map((n) => n.coin).filter(coin => coin !== 'BTC');
        }
        setSelected(newSelected);

        portfolio.forEach((coin) => {
            handleSlider(coin.slider, coin.coin, newSelected);
        })
    };

    const handleClick = (coin) => {
        if (coin !== 'BTC') {
            const selectedIndex = selected.indexOf(coin);

            let newPortfolio = [...portfolio];
            let match = newPortfolio.find(c => c.coin === coin);
            match.excluded = (selectedIndex === -1);
            setPortfolio(newPortfolio);

            let newSelected = [];

            if (selectedIndex === -1) {
                newSelected = newSelected.concat(selected, coin);
            } else if (selectedIndex === 0) {
                newSelected = newSelected.concat(selected.slice(1));
            } else if (selectedIndex === selected.length - 1) {
                newSelected = newSelected.concat(selected.slice(0, -1));
            } else if (selectedIndex > 0) {
                newSelected = newSelected.concat(
                    selected.slice(0, selectedIndex),
                    selected.slice(selectedIndex + 1),
                );
            }

            setSelected(newSelected);
            handleSlider(match.slider, match.coin, newSelected);
        }
    };

    const handleSlider = (value, coin, select = selected) => {
        let newPortfolio = [...portfolio];
        let match = newPortfolio.find(c => c.coin === coin);
        match.slider = value;

        let totalSlider = 0;
        let numSlider = 0;
        newPortfolio.forEach((coin) => {
            if (select.indexOf(coin.coin) === -1) {
                totalSlider += coin.slider;
                if (coin.slider === -1) totalSlider++;
                if (coin.slider !== -1) numSlider++;
            }
        });
        let maxSlider;
        const remainingSlider = 100 - totalSlider;
        const numCoinsZero = newPortfolio.length - numSlider - select.length;
        maxSlider = (numCoinsZero === 0) ? totalSlider : Math.max(100, totalSlider);
        newPortfolio.forEach((coin) => {
            coin.excluded = false;
            if (select.indexOf(coin.coin) !== -1) {
                coin.distribution = (0).toFixed(precision);
                coin.excluded = true;
            } else if (maxSlider <= 100) {
                if (coin.slider === -1) coin.distribution = (remainingSlider / numCoinsZero).toFixed(precision);
                else coin.distribution = (coin.slider / maxSlider * 100).toFixed(precision);
            } else {
                if (coin.slider === -1) coin.distribution = (0).toFixed(precision);
                else coin.distribution = (coin.slider / maxSlider * 100).toFixed(precision);
            }
        });

        setPortfolio(newPortfolio);
        onInput(newPortfolio);
    };

    const labelFormatter = (value) => {
        if (value === -1) return '=';
        return value;
    }

    const isSelected = (coin) => selected.indexOf(coin) !== -1;

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        Coin Settings
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <HtmlTooltip
                        title={
                            <React.Fragment>
                                <Typography color="inherit" variant="h6">Hints</Typography>
                                <div>&nbsp;</div>
                                <div><b>{"Exclude coins"}</b></div>
                                <div>{"Use the checkboxes on the left to exclude coins in your account from balancing. Balancing Bot will completely ignore selected coins."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Coin distribution"}</b></div>
                                <div>{"By default, all coins are balanced with an equal distribution. If you'd like to change the target distribution of your coins, use the sliders. Coins with their slider all the way to the left will get an even distribution up to a total of 100%."}</div>
                            </React.Fragment>
                        }
                    >
                        <InfoOutlinedIcon />
                    </HtmlTooltip>
                </Grid>
            </Grid>
            <Grid container spacing={3}>
                <Paper className={classes.paper} elevation={0}>

                    {(portfolioReceived) ? (
                        <div>
                            <EnhancedTableToolbar selected={selected} portfolio={portfolio} setSelected={setSelected} />
                            < Table
                                className={classes.table}
                                size="small"
                                aria-labelledby="tableTitle"
                                aria-label="enhanced table"
                            >
                                <EnhancedTableHead
                                    classes={classes}
                                    numSelected={selected.length}
                                    onSelectAllClick={handleSelectAllClick}
                                    rowCount={portfolio.length}
                                />
                                <TableBody>
                                    {portfolio.map((coin, index) => {
                                        const isItemSelected = isSelected(coin.coin);
                                        const labelId = `enhanced-table-checkbox-${index}`;
                                        let checkbox;
                                        if (coin.coin !== 'BTC') {
                                            checkbox = <Checkbox
                                                checked={isItemSelected}
                                                inputProps={{ 'aria-labelledby': labelId }}
                                            />
                                        } else {
                                            checkbox = null;
                                        }

                                        return (
                                            <TableRow
                                                hover
                                                // onClick={(event) => handleClick(event, coin.coin)}
                                                role="checkbox"
                                                aria-checked={isItemSelected}
                                                tabIndex={-1}
                                                key={coin.coin}
                                                selected={isItemSelected}
                                            >
                                                <TableCell padding="checkbox" onClick={(event) => handleClick(coin.coin)}>
                                                    {checkbox}
                                                </TableCell>
                                                <TableCell component="th" id={labelId} scope="row" padding="none" onClick={(event) => handleClick(coin.coin)}>
                                                    {coin.coin}
                                                </TableCell>
                                                <TableCell align="right">
                                                    <MySlider
                                                        value={coin.slider}
                                                        step={1}
                                                        valueLabelDisplay="on"
                                                        marks={marks}
                                                        min={-1}
                                                        valueLabelFormat={value => <div>{labelFormatter(value)}</div>}
                                                        onChange={(event, value) => handleSlider(value, coin.coin)}
                                                    />
                                                </TableCell>
                                                <TableCell component="th" className={classes.cell} id={labelId} scope="row" padding="none" align="left" onClick={(event) => handleClick(coin.coin)}>
                                                    {coin.distribution}%
                                        </TableCell>
                                                <TableCell component="th" className={classes.cell2} id={labelId} scope="row" padding="none" align="left">
                                                    {(coin.coin === 'BTC' && parseFloat(coin.distribution) === 0) ? (
                                                        <Typography variant="overline" color="secondary">Cannot be 0%</Typography>
                                                    ) : (null)
                                                    }
                                                </TableCell>
                                            </TableRow>
                                        )
                                    })}
                                </TableBody>
                            </Table>
                        </div>
                    ) : (
                        <div>
                            <Box pt={4}></Box>
                            <Typography>Loading account data...</Typography>
                            <Box pt={4}></Box>
                            <Skeleton animation="wave" />
                            <Skeleton animation="wave" />
                            <Skeleton animation="wave" />
                        </div>
                    )}
                </Paper>
            </Grid>
        </React.Fragment>
    );
}