import React, { useContext, useState, useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Link, Switch, Route, BrowserRouter, Redirect } from 'react-router-dom';
import DMSwitch from '@material-ui/core/Switch';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core";
import { useLocalStore } from "../services/LocalStoreService";
import { FiatContext } from '../context/FiatContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ServerContext } from '../context/ServerContext';

import { routesUp, routesDown } from '../routes/Routes';

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  logo: {
    maxWidth: 60,
    padding: 10,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    height: '100%',
  },
  drawerContainer: {
    overflow: 'auto',
  },
  content: {
    justify: "flex-start",
    alignItems: "flex-start",
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
  bottomPush: {
    position: "fixed",
    bottom: 0,
    paddingBottom: 30,
  },
}));

export default function Main() {
  const [mode, setMode] = useLocalStore('mode', 'light');
  const setFiatStore = useLocalStore('fiat', 'EUR')[1];
  const [fiat, setFiat] = useContext(FiatContext);
  const configuration = useContext(ConfigurationContext)[0];
  const { status, updated } = useContext(ServerContext)[0];

  const [open, setOpen] = useState(true);
  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    setOpen(status !== 'connected' || Object.keys(configuration).length === 0)
    return () => {
    }
  }, [status, configuration]);

  const classes = useStyles();

  const handleDarkmode = (e) => {
    (mode === 'light') ? setMode('dark') : setMode('light');
  }

  const handleFiat = (e) => {
    if (fiat === 'EUR') {
      setFiat('USD');
      setFiatStore('USD');
    } else {
      setFiat('EUR');
      setFiatStore('EUR');
    }
  }

  const theme = createMuiTheme({
    palette: {
      type: mode
    }
  });

  const StyledButton = withStyles({
    root: {
      '&:hover': {
        backgroundColor: 'transparent',
      },
      '&:click': {
        backgroundColor: '#f00',
      },
    },
  })(Button);

  return (
    <ThemeProvider theme={theme}>
      <Backdrop className={classes.backdrop} open={open} onClick={handleClose}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <BrowserRouter>
        <div className={classes.root}>
          <CssBaseline />
          <AppBar position="fixed" className={classes.appBar}>
            <Toolbar>
              <img src="/logo192.png" alt="Balance-Bot Logo" className={classes.logo} />
              <Typography variant="h6" noWrap>
                Balance Bot
          </Typography>
            </Toolbar>
          </AppBar>
          <Drawer className={classes.drawer} variant="permanent">
            <Paper className={classes.drawerPaper}>
              <Grid container direction="column">
                <Grid item xs={12}>
                  <Toolbar />
                  {(configuration.apiKey !== '') ? (
                    <div className={classes.drawerContainer}>
                      <List>
                        {routesUp.map((prop, key) => {
                          return (
                            <Link to={prop.path} style={{ textDecoration: 'none' }} key={key}>
                              <ListItem button key={prop.sidebarName}>
                                <ListItemIcon><prop.icon /></ListItemIcon>
                                <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                              </ListItem>
                            </Link>
                          );
                        })}
                      </List>
                      <Divider />
                      {routesDown.map((prop, key) => {
                        return (
                          <Link to={prop.path} style={{ textDecoration: 'none' }} key={key}>
                            <ListItem button key={prop.sidebarName}>
                              <ListItemIcon><prop.icon /></ListItemIcon>
                              <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                            </ListItem>
                          </Link>
                        );
                      })}
                    </div>
                  ) : (
                    <div className={classes.drawerContainer}>
                      <List>
                        {routesUp.map((prop, key) => {
                          return (
                            <Redirect to={'/configuration'} style={{ textDecoration: 'none' }} key={key}>
                              <ListItem button key={prop.sidebarName}>
                                <ListItemIcon><prop.icon /></ListItemIcon>
                                <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                              </ListItem>
                            </Redirect>
                          );
                        })}
                      </List>
                      <Divider />
                      {routesDown.map((prop, key) => {
                        return (
                          <Link to={prop.path} style={{ textDecoration: 'none' }} key={key}>
                            <ListItem button key={prop.sidebarName}>
                              <ListItemIcon><prop.icon /></ListItemIcon>
                              <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                            </ListItem>
                          </Link>
                        );
                      })}
                    </div>
                  )}
                </Grid>
                <div className={classes.bottomPush}>
                  <Box pl={1}>
                    <Grid container item xs={12} alignItems="center" justify="flex-end">
                      <Grid item xs={8}>
                        <FormControlLabel
                          onChange={handleDarkmode}
                          value={mode}
                          control={<DMSwitch checked={mode === 'dark'} color="secondary" />}
                          label={<Typography variant="button">Light</Typography>}
                          labelPlacement="start"
                        />
                      </Grid>
                      <Grid item xs={4}>
                        <StyledButton disableRipple onClick={handleDarkmode}><Typography variant="button">Dark</Typography></StyledButton>
                      </Grid>
                    </Grid>

                    <Grid container item xs={12} alignItems="center" justify="flex-end">
                      <Grid item xs={8}>
                        <FormControlLabel
                          onChange={handleFiat}
                          value={fiat}
                          control={<DMSwitch checked={fiat === 'EUR'} color="secondary" />}
                          label={<Typography variant="button">USD&nbsp;&nbsp;&nbsp;</Typography>}
                          labelPlacement="start"
                        />
                      </Grid>
                      <Grid item xs={4}>
                        <StyledButton disableRipple onClick={handleFiat}><Typography variant="button">EUR</Typography></StyledButton>
                      </Grid>
                    </Grid>
                  </Box>
                </div>
              </Grid>
            </Paper>
          </Drawer>
          <main className={classes.content}>
            <Toolbar />
            <Switch>
              {routesUp.map((prop, key) => {
                return (
                  <Route exact path={prop.path} component={prop.component} key={key} />
                );
              })}
              {routesDown.map((prop, key) => {
                return (
                  <Route exact path={prop.path} component={prop.component} key={key} />
                );
              })}
            </Switch>
          </main>
        </div>
      </BrowserRouter>
    </ThemeProvider>
  );
}