import { ResponsiveLine } from '@nivo/line'
import { useTheme } from '@material-ui/core/styles';

export default function LineChart(props) {
    const mode = useTheme().palette.type;

    let performance = props.performance;
    const balances = props.balances;
    const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
    let coins = [];
    let data = [];
    let prices = [];

    rows.forEach((coin, i) => {
        coins.push(coin.coin);
        prices[i] = {
            coin: coin.coin,
            initial: 0,
            data: [],
        };
    });

    if (performance !== []) {
        coins.forEach((coin, i) => {
            if (coin !== 'BTC') {
                performance.forEach((day) => {
                    day.coins.forEach((c) => {
                        if (c.coin === coin) {
                            if (prices[i].initial === 0) prices[i].initial = c.price;
                            prices[i].data.push({
                                x: day.date,
                                y: (c.price / prices[i].initial - 1) * 100,
                            });
                        }
                    });
                });
                data.push({
                    id: coin,
                    data: prices[i].data,
                })
            }
        });
    }

    const getTheme = () => {
        return {
            axis: {
                ticks: {
                    line: {
                        stroke: (mode === 'light') ? "#000000" : "#FFFFFF"
                    },
                    text: { fill: (mode === 'light') ? "#000000" : "#FFFFFF" }
                },
                legend: {
                    text: {
                        fill: (mode === 'light') ? "#000000" : "#FFFFFF"
                    }
                }
            },
            crosshair: {
                line: {
                    stroke: (mode === 'light') ? "#000000" : "#FFFFFF",
                },
            },
            legends: {
                text: {
                    fill: (mode === 'light') ? "#000000" : "#FFFFFF",
                }
            },
            tooltip: {
                container: {
                    background: (mode === 'light') ? "#FFFFFF" : "#000000",
                    color: (mode === 'light') ? "#000000" : "#FFFFFF",
                }
            }
        };
    };

    function uniq(a) {
        var seen = {};
        return a.filter(function (item) {
            return seen.hasOwnProperty(item) ? false : (seen[item] = true);
        });
    }

    return (
        <div className="wrapper">
            <div><b>{props.title}</b></div>
            <div className="graphContainerCoins">
                <ResponsiveLine
                    data={data}
                    sliceTooltip={({ slice }) => {
                        let background = (mode === 'light') ? "#FFFFFF" : "#000000";
                        return (
                            <div
                                style={{
                                    background: background,
                                    boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)',
                                    padding: '9px 12px',
                                }}
                            >
                                {/* <div>x: {slice.id}</div> */}
                                {uniq(slice.points).map(point => (
                                    <div
                                        key={point.id}
                                        style={{
                                            color: point.serieColor,
                                            padding: '3px 0',
                                        }}
                                    >
                                        {point.serieId} <strong>{point.data.yFormatted}%</strong>
                                    </div>
                                ))}
                            </div>
                        )
                    }}
                    theme={getTheme()}
                    margin={{ top: 50, right: 110, bottom: 50, left: 80 }}
                    xScale={{
                        type: 'time',
                        format: '%Y/%m/%d',
                        useUTC: false,
                        precision: 'day',
                    }}
                    xFormat="time:%Y/%m/%d"
                    yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
                    yFormat=" >-.2f"
                    curve="monotoneX"
                    lineWidth={4}
                    colors={{ scheme: 'red_yellow_blue' }}
                    axisTop={null}
                    axisRight={null}
                    axisBottom={null}
                    axisLeft={{
                        legend: 'Change (%)',
                        legendOffset: -70,
                        legendPosition: 'middle',
                    }}
                    enablePoints={false}
                    enableGridX={false}
                    enableGridY={false}
                    useMesh={true}
                    // enableSlices="y"
                    crosshairType="cross"
                    legends={[
                        {
                            anchor: 'bottom-right',
                            direction: 'column',
                            justify: false,
                            translateX: 100,
                            translateY: 0,
                            itemsSpacing: 0,
                            itemDirection: 'left-to-right',
                            itemWidth: 80,
                            itemHeight: 20,
                            itemOpacity: 0.75,
                            symbolSize: 12,
                            symbolShape: 'circle',
                            symbolBorderColor: 'rgba(0, 0, 0, .5)',
                            itemTextColor: "#ff0000",
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemBackground: 'rgba(0, 0, 0, .03)',
                                        itemOpacity: 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
            </div>
        </div>
    )
}