import { useEffect, useContext } from "react";
import { useSnackbar } from 'notistack';
import { socket } from '../context/SocketContext';
import { ServerContext } from '../context/ServerContext';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { add } from 'date-fns'

export default function SocketService() {
    let [{ status, updated }, setServer] = useContext(ServerContext);
    const [balances, setBalances] = useContext(BalancesContext);
    const [performance, setPerformance] = useContext(PerformanceContext);
    const [configuration, setConfiguration] = useContext(ConfigurationContext);
    const [configurationInput, setConfigurationInput] = useContext(ConfigurationInputContext);
    const setStartingBalance = useContext(StartingBalanceContext)[1];

    const { enqueueSnackbar } = useSnackbar();

    const handleEvent = (log, summary) => {
        const variant = (log.type === 'order') ? 'info' : log.type;
        enqueueSnackbar(summary, {
            variant: variant,
            autoHideDuration: 8000,
        });
    };

    const updatePerformance = () => {
        const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];
        let update = [];

        rows.forEach((coin) => {
            update.push({
                coin: coin.coin,
                number: coin.number,
                price: coin.last,
            });
        });
        if (performance.length !== 0) {
            // console.log(performance[performance.length - 1].date)
            // console.log(getDate())
            if (performance[performance.length - 1].date === getDate()) {
                let newPerformance = [...performance];
                newPerformance[newPerformance.length - 1].coins = update;
                newPerformance[newPerformance.length - 1].eur = balances.eur;
                newPerformance[newPerformance.length - 1].usd = balances.usd;
                setPerformance(newPerformance);
            } else {
                // It's a new day, the server will send an update automatically
                console.log(getTimeStamp() + ' | new day, waiting for performance update');
            }
        }
    }

    const shiftedDate = () => {
        const now = new Date();
        let adjusted = now;
        if (configuration.timeshift && configuration.timeshift !== 0) adjusted = add(now, { hours: configuration.timeshift });
        return adjusted;
    }

    const getDate = () => {
        const now = shiftedDate();

        return now.getFullYear() + "/" + (now.getMonth() + 1).toString().padStart(2, '0') + "/" + now.getDate().toString().padStart(2, '0');
    }

    function getTimeStamp() {
        const now = shiftedDate();

        return now.getFullYear() + "/" + (now.getMonth() + 1).toString().padStart(2, '0') + "/" + now.getDate().toString().padStart(2, '0') + " " + now.getHours().toString().padStart(2, '0') + ":" + now.getMinutes().toString().padStart(2, '0') + ":" + now.getSeconds().toString().padStart(2, '0');
    }

    useEffect(() => {
        updatePerformance();
        return () => {
        }
    }, [balances]);

    useEffect(() => {
        socket.on("status", data => {
            setServer({
                status: data,
                updated: updated,
            });
        });
        socket.on("balances", data => {
            setBalances(data);
            setServer({
                status: 'connected',
                updated: data.time,
            });
        });
        socket.on("performance", (logs) => {
            setPerformance(logs);
        });
        socket.on("configuration", (config) => {
            setConfiguration(config);
            setConfigurationInput({ ...config, recreate: false });
        });
        socket.on("starting-balances", (sb) => {
            setStartingBalance(sb);
        });
        socket.on("event", function (log, summary) {
            handleEvent(log, summary);
        });
        socket.on("disconnect", data => {
            setServer({
                status: 'disconnected',
                updated: updated,
            });
        });

        return () => {
            socket.disconnect();
        }
    }, []);

    return (null);
}