import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { DataGrid } from '@material-ui/data-grid';
import { format } from 'date-fns';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';

const columns = [
    { field: 'coin', headerName: 'Coin', width: 350, editable: false },
    { field: 'startnumber', headerName: 'Number', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
    { field: 'startprice', headerName: 'Price (BTC)', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
];

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    header: {
        "& .MuiDataGrid-colCell":
            theme.palette.type === 'light'
                ? {
                    backgroundColor: '#aaaaaa',
                }
                : {
                    backgroundColor: '#777777',
                },
    },
    paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
    },
}));

export default function ConfigP3(props) {
    const classes = useStyles();

    let newStartPerformance = [];

    const [input, setInput] = useContext(ConfigurationInputContext);
    const newPortfolio = input.portfolio;
    const [portfolio, setPortfolio] = useState(newPortfolio);
    const [selectedDate, setSelectedDate] = useState(input.startdate);
    const [recreate, setRecreate] = useState(input.recreate);

    newPortfolio.forEach((coin) => {
        if (!coin.excluded) {
            newStartPerformance.push({
                id: coin.coin,
                coin: coin.coin,
                startnumber: Number((coin.startnumber).toFixed(8)),
                startprice: Number((coin.startprice).toFixed(8)),
            });
        }
    });

    const [startPerformance, setStartPerformance] = useState(newStartPerformance);

    const handleDateChange = (input) => {
        const date = format(input, "yyyy/MM/dd");
        props.callback({
            field: 'startdate',
            input: date,
        });
        setSelectedDate(date);
        setRecreate(true);
    };

    const handleCheck = (e) => {
        const checked = e.target.checked;
        props.callback({
            field: 'recreate',
            input: checked,
        });
        setRecreate(checked);
    }

    const handleEditCellChangeCommitted = (e) => {
        let newPortfolio = [...portfolio];
        let match = newPortfolio.find(c => c.coin === e.id);
        match[e.field] = Number(e.props.value);

        setPortfolio(newPortfolio);
        props.callback({
            field: 'portfolio',
            input: newPortfolio,
        });
        setRecreate(true);
    }

    function CustomToolbar() {
        return (
            <Grid container alignItems="flex-end">
                <Grid item xs={5}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <KeyboardDatePicker
                            disableToolbar
                            variant="inline"
                            format="yyyy/MM/dd"
                            margin="normal"
                            id="date-picker-inline"
                            label="Set start date"
                            value={selectedDate}
                            onChange={handleDateChange}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />
                    </MuiPickersUtilsProvider>
                </Grid>
                <Grid item xs={2}></Grid>
                <Grid item xs={5}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="rewrite" value="yes" checked={recreate} onChange={handleCheck} />}
                        label="Recreate performance history"
                    />
                </Grid>
            </Grid>
        );
    }

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        Starting Balance
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <HtmlTooltip
                        title={
                            <React.Fragment>
                                <Typography color="inherit" variant="h6">Hints</Typography>
                                <div>&nbsp;</div>
                                <div><b>{"Use of starting balance"}</b></div>
                                <div>{"Balancing Bot uses the starting balance to determine the performance of your balancing portfolio. It compares the current portfolio value with the starting portfolio value to calculate the change."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Default starting balance"}</b></div>
                                <div>{"By default, Balancing Bot will check the number of coins in your account and their prices upon first-time configuration, and use these to calculate the starting balance."}</div>
                                <div>&nbsp;</div>
                                <div><b>{"Customize starting balance"}</b></div>
                                <div>{"If you started balancing at an earlier point in time, you can manually set the starting balance for your account. Do so by filling in the number of coins and their prices at which you purchased those coins. Simply click on the cells in the table to edit. Tick the 'Recreate performance history' checkbox to confirm you want to recreate your performance history."}</div>
                                <div>&nbsp;</div>
                                <div>{"Make sure to set the start date "}<b>{"after"}</b>{" the date when you bought the coins in your portfolio, to avoid these purchases to be counted twice."}</div>
                                <div>&nbsp;</div>
                                <div>{"Depending on the start date and your trade history, it may take several minutes to recreate your performance history and until this is visible on the performance screen. You will receive a notification message once the process has completed."}</div>
                                <div>&nbsp;</div>
                                <div><i>{"Note: "}</i>{"This operation will also recreate your event history, including all executed orders since the start date, but excluding error messages. You can set a new starting balance and date at anytime."}</div>
                            </React.Fragment>
                        }
                    >
                        <InfoOutlinedIcon />
                    </HtmlTooltip>
                </Grid>
            </Grid>
            <div style={{ width: '752px' }}>
                <CustomToolbar />
                <Box pt={3}>
                    <Grid container spacing={3}>
                        <Paper className={classes.paper} elevation={0}>
                            <div style={{ width: '752px' }}>
                                <DataGrid className={classes.header} rows={startPerformance} columns={columns}
                                    autoHeight={true}
                                    density='compact'
                                    disableColumnSelector={true}
                                    disableColumnMenu={true}
                                    hideFooter={true}
                                    hideFooterPagination={true}
                                    hideFooterRowCount={true}
                                    onEditCellChangeCommitted={handleEditCellChangeCommitted}
                                />
                            </div>
                        </Paper>
                    </Grid>
                </Box>
            </div>
        </React.Fragment>
    );
}