import React, { useState, useContext } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';

export default function ConfigP1(props) {

    const [input, setInput] = useContext(ConfigurationInputContext);
    const [apiKey, setApiKey] = useState(input.apiKey);
    const [apiSecret, setApiSecret] = useState(input.apiSecret);

    const onInput = (e) => {
        props.callback({
            field: e.target.id,
            input: e.target.value
        });

        if (e.target.id === 'apiKey') setApiKey(e.target.value);
        if (e.target.id === 'apiSecret') setApiSecret(e.target.value);
    }

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        Exchange API Details
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <HtmlTooltip
                        title={
                            <React.Fragment>
                                <Typography color="inherit" variant="h6">Hints</Typography>
                                <div>&nbsp;</div>
                                {"If you haven't done so already, logon to your exchange, go to API Management and create a new API. Then enter the API key and API secret here."}
                                <div>&nbsp;</div>
                                <div><i>{"Note: "}</i>{"You only need to enter the API details once, as your API details will be stored on the server for later use. If you want to change the configuration settings at a later point, you can just click the Next button and reuse the API that you've already configured previously. If you want to change to a different API, you can override the original API by entering the new details here."}</div>
                            </React.Fragment>
                        }
                    >
                        <InfoOutlinedIcon />
                    </HtmlTooltip>
                </Grid>
            </Grid>
            <form style={{ minWidth: '100%' }}>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="apiKey"
                            name="apiKey"
                            label="API Key"
                            type="password"
                            value={apiKey}
                            onChange={onInput}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="apiSecret"
                            name="apiSecret"
                            label="API Secret"
                            type="password"
                            value={apiSecret}
                            onChange={onInput}
                            fullWidth
                        />
                    </Grid>
                </Grid>
            </form>
        </React.Fragment>
    );
}